:root {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently supported by Chrome, Opera and Firefox */
}

.centered {
    display: flex;
    align-items: center;
    justify-content: center;
}

.card-elements-container {
    border-radius: 16px;
    -webkit-box-shadow: 0 0 8px 8px rgba(0, 0, 0, 0.022);
    box-shadow: 0 0 8px 8px rgba(0, 0, 0, 0.022);
    width: 100%;
}

.card-elements-container .tappable_top {
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
}

.card-elements-container .tappable_bottom {
    border-bottom-left-radius: 16px;
    border-bottom-right-radius: 16px;
}

.card-elements-container .cell {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 14px 14px;
}

.card-elements-container .left {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
}

.Link {
    color: inherit;
}
.CellButton {
    color: inherit;
}

.card-elements-container .text {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.29;
    letter-spacing: -0.4px;
    margin-left: 8px;
}

.card-elements-container .right {
    align-self: center;
}